import React from 'react';
import { BrowserRouter as Router,Switch,Route,useRouteMatch} from "react-router-dom";
import NotFound from "./404";
import BannerVideo from '../components/Banner/BannerVideo';
import BannerSecondJob from '../components/Jobs2/BannerSecondJob';
import JobsTab from '../components/Jobs2/JobsTab';
import MetaDecorator from '../components/MetaDatos/MetaDecorator';
import ReactGA from 'react-ga';
import "../components/Jobs2/jobsnew.css"
import "../components/Jobs/jobs.css"
import Dreamcoders from "../components/Jobs2/Dreamcoders";


/* Page */
import JobsSingle from '../components/Jobs2/JobsSingle';
import BannersJobs from '../components/Beneficios/BannersJobs';
import Benefits from './Benefits';

const JobsNew = () => {

    let { path } = useRouteMatch();

    // Configuraci√≥n de Google Analytics
    const gaTrackingId = "UA-194698672-1"; 
    ReactGA.initialize (gaTrackingId); 
    ReactGA.pageview ("/jobs");

    return (
        <>
            <MetaDecorator 
                meta_title="metaTitle_Jobs"
                meta_description="metaDescription_Jobs"
                url="jobs"
            />
            <Switch>
                <Route exact path={path}>
                    <BannerVideo
                        title_banner2 = "title_banner_Jobs"
                        idBanner="bannerBlog"
                        classBanner2="banner_job "
                    />
                    <BannerSecondJob 
                        BannerTitle = "BannerTitle"
                        BannerSubTitle = "BannerSubTitle"
                        BannerSubTitle2 = "BannerSubTitle2"
                        BannerTxt = "BannerTxt"
                    />
                    <JobsTab titleCareers="titleCareers"/>
                    <Dreamcoders DreamcodersTitle="DreamcodersTitle"/>
                    <BannersJobs 
                        BannerEnd = "BannerEnd"
                        read_more = "read_more"
                    />
                    <a className="nav-link" href="/benefits" >Beneficios</a>

                </Route>
                <Route path={`${path}/:topicId`} component={JobsSingle}/>
                <Route path={`${path}/benefits`} component={Benefits} />
                <Route path="/404" component={NotFound} /> 
                <Route path="*" component={NotFound} />  
            </Switch>
        </>
    )
}

export default JobsNew;