import React from 'react';
import NotFound from "./404";
import {BrowserRouter as Router,Switch,Route,useRouteMatch} from "react-router-dom";
import BlogBanner from '../components/Banner/BlogBanner';
import CategoryItem from '../components/Categorys/CategoryItem';
import "../components/Categorys/category.css"
import ReactGA from 'react-ga';
import MetaDecorator from '../components/MetaDatos/MetaDecorator';

/* Page */
import Categorys from '../components/Blog/Categorys';


const categoryEn = require('../assets/translations/en.json');
const categoryEs = require('../assets/translations/es.json');

let category = null;

const Category = (props) => {

    let { path } = useRouteMatch();



        // Configuraci√≥n de Google Analytics
        const gaTrackingId = "UA-194698672-1"; 
        ReactGA.initialize (gaTrackingId); 
        ReactGA.pageview ("/category" );

    if (props.language === 'en') {
        category = categoryEn
    } else {
        category = categoryEs
    }

    return(
        <>  
            <MetaDecorator 
                meta_title="metaTitle_Home"
                meta_description="metaDescription_Home"
                url="category"
            />
            <Switch>
                <Route exact path={path}>
                    <BlogBanner title_banner2 = "title_banner_category"
                        idBanner="bannerBlog"
                        classBanner2="banner_category"
                    />
                    <div className=' pb-5 category'>
                        <div className="container">
                            <div className="row scrum_team_inner pt-5">
                            { 
                            category.categoryList.map((item, i) => {
                                return (                                      
                                    <CategoryItem key={i}
                                        image={item.url}
                                        title={item.text}
                                        url={item.url}
                                    />
                                    )
                                })
                            }
                            </div>
                        </div>
                    </div>    
                </Route>
                <Route path={`${path}/:topicId`} component={Categorys}/>
                {/* <Route path="*" component={NotFound} />   */}
            </Switch>
        </>
    )
}

export default Category;



