import React, {Component} from 'react';

class DreamcodersItem extends Component {
    render(){
        var {teamImage, memberd ,memberN, verb}= this.props;
        return(
            <div className="ex_team_item">
                <img src={require ("../../img/dreamcoders/" + teamImage).default} alt="team"/>
                <div className="team_content text-left pl-3 ">
                    <a href=".#">
                        <h3 className="f_p f_size_25 f_600 w_color">{memberN}</h3>
                    </a>
                    <h5 className='w_color f_p f_size_18 f_500 w_color'>{memberd}</h5>
                </div>
                <div className="hover_content">
                    <div className="n_hover_content"> 
                        <a href=".#">
                            <h3 className="mb-4 verb_team f_p f_size_30 f_600 w_color">{verb}</h3>
                        </a>
                    </div>
                </div>
            </div>
        )
    }
}
export default DreamcodersItem;