import React, { Component } from "react";
import { translate } from "react-translate";


class JobsItems extends Component {
  render() {

    let {¬†nameJob, url , id, color, t } = this.props;


    return (
              <div className="col-lg-4 p-1" id={id}>
                <div className={`media ${"bg_" + color} flex-wrap`} >
                    <div className="media-body  w_color">
                      <h1 className="f_size_30 f_700" >{nameJob}</h1>   
                      <h2 className="f_size_25 pt-2 pb-2 f_600 ">Remoto<i className="pl-2 f_size_15 f_600 ti-rss-alt"></i></h2>
                      <button type="button" class="f_p f_700 btn btn-light">
                            <a href={"/careers/"+ url} className={`${color}`} >{t(this.props.apply)}</a>
                      </button>
                    </div>
                </div>  
              </div>            
    );
  }
}
export default translate('careers')(JobsItems);
