import React, {Component} from 'react';
import SeoTitle from '../Title/SeoTitle';
import { translate } from "react-translate";
import "./testimonial.css"

import { Navigation, Pagination } from "swiper";
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react';

const TestimonialEn = require('../../assets/translations/en.json');
const TestimonialEs = require('../../assets/translations/es.json');
let testimonial = null;

class Testimonial extends Component{

   render(){


    let {¬†t, language } = this.props;

    if (language === 'en') {
        testimonial = TestimonialEn
      } else {
        testimonial = TestimonialEs
      }

    
    return(
            <section className=" sec_pad " id="testimonial">
                    <SeoTitle  Title={t(this.props.testim_title)} Tclass="pb-4 text-center" Tclass2="t_color" />
                <div className="container ">
                    <Swiper
                        slidesPerView={1}
                        spaceBetween={30}
                        loop={true}
                        pagination={{
                        clickable: true,
                        }}
                        navigation={false}
                        breakpoints={{
                            640: {
                              slidesPerView: 2,
                              spaceBetween: 20,
                            },
                            768: {
                              slidesPerView: 2,
                              spaceBetween: 40,
                            },
                            1024: {
                              slidesPerView: 2,
                              spaceBetween: 50,
                            },
                          }}
                        modules={[Pagination, Navigation]}
                        className="swiper-testimonios"
                    >
                        {
                            testimonial.testimonios.testimoniosItems.map((item, i) => {
                                return (
                                    <SwiperSlide className='testimonial'>                          
                                        <div className="erp_testimonial_item ">
                                            <div className="content"><p>{item.DescripT}</p></div>
                                            <div className="media"><div className="media-body"><h5>{item.NameT}</h5><p>{item.Position}</p></div></div>
                                        </div>
                                    </SwiperSlide>
                                )
                            })
                        }
                    </Swiper>
                </div>
            </section>
        )
   }
}

export default translate('testimonios')(Testimonial);

